@font-face {
  font-family: "M PLUS 1 Code";
  src: url("./assets/fonts/MPLUS1Code-VariableFont_wght.ttf");
}

:root {
  --fs-base: 17px;
  --bg: #0b0f12;
  --text: #eef2f6;
  --muted: #a3abb4;
  --accent: #6ee7b7;
  --accent-2: #7cc8ff;
  --danger: #ff6b6b;
  --border-glass: rgba(255,255,255,0.15);
  --glass-bg: rgba(255,255,255,0.05);
  --radius: 18px;
  --gap: 1.25rem;
}

/* ===== ベース設定 ===== */
html, body {
  margin: 0;
  padding: 0;
  background: var(--bg);
  color: var(--text);
  font-family: Inter, system-ui, sans-serif;
  font-size: var(--fs-base);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  font-family: "M PLUS 1 Code";
}
*, *::before, *::after { box-sizing: border-box; }

.container {
  max-width: 1200px;
  margin-inline: auto;
  padding: 1rem;
}

a.link {
  color: var(--accent-2);
  text-decoration: none;
  transition: color 0.2s ease;
}

a.link:hover {
  color: var(--accent);
}

hr {
  border: none;
  border-top: 1px solid rgba(255,255,255,0.08);
  margin: 1rem 0;
}

/* ===== ガラス効果のベースクラス ===== */
.glass {
  background: var(--glass-bg);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  border: 1px solid var(--border-glass);
  border-radius: var(--radius);
  box-shadow: 0 8px 32px rgba(0,0,0,0.3);
}

/* ===== カード ===== */
.card {
  composes: glass;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.card__head, .card__foot {
  padding: 1rem;
  background: rgba(255,255,255,0.03);
  border-bottom: 1px solid rgba(255,255,255,0.08);
}
.card__body {
  padding: 1rem;
}

/* ===== ボックス ===== */
.box {
  composes: glass;
  padding: 1rem;
  border-radius: var(--radius);
}

/* ===== テキスト ===== */
.title {
  font-size: 1.35rem;
  font-weight: 700;
  margin-bottom: .5rem;
}
.text {
  font-size: 1rem;
  color: var(--muted);
}

/* ===== ボタン ===== */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 48px;
  padding: 0 1rem;
  font-size: 1rem;
  border-radius: var(--radius);
  border: 1px solid rgba(255,255,255,0.15);
  background: rgba(255,255,255,0.08);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  color: var(--text);
  cursor: pointer;
  transition: background 0.2s ease;
}
.btn:hover {
  background: rgba(255,255,255,0.15);
}
.btn--accent {
  background: linear-gradient(135deg, rgba(110,231,183,0.3), rgba(124,200,255,0.3));
  color: #fff;
  border: none;
}

/* ===== 入力 ===== */
.input, input, textarea, select {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  border-radius: var(--radius);
  border: 1px solid var(--border-glass);
  background: rgba(255,255,255,0.05);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  color: var(--text);
}

/* ===== バッジ ===== */
.badge {
  display: inline-block;
  padding: 0.3rem 0.75rem;
  font-size: 0.85rem;
  border-radius: 999px;
  background: rgba(255,255,255,0.08);
  border: 1px solid var(--border-glass);
  backdrop-filter: blur(8px);
  color: var(--muted);
}

/* ===== テーブル ===== */
.table-wrapper {
  overflow-x: auto;
}
.table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255,255,255,0.02);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--border-glass);
  border-radius: var(--radius);
  overflow: auto;
}
.table th, .table td {
  padding: 0.85rem 1rem;
  text-align: left;
  border-bottom: 1px solid rgba(255,255,255,0.08);
  font-size: 1rem;
}
.table thead th {
  background: rgba(255,255,255,0.05);
  color: var(--text);
  font-weight: 600;
}
.table tbody tr:hover td {
  background: rgba(255,255,255,0.06);
}

/* ===== 見出しなし時間割（横スクロール） ===== */
.timetable {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255,255,255,0.02);
  backdrop-filter: blur(12px);
  border: 1px solid var(--border-glass);
  border-radius: var(--radius);
  overflow-x: auto;
}
.timetable td {
  padding: 0.85rem 1rem;
  text-align: center;
  border-bottom: 1px solid rgba(255,255,255,0.08);
  font-size: 1rem;
}
.timetable th {
  text-align: center;
}
.timetable tr:nth-child(odd) {
  background: rgba(255,255,255,0.03);
}
.timetable tr:nth-child(even) {
  background: rgba(255,255,255,0.05);
}

/* スマホは横スクロール */
.table-scroll {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.table-scroll table {
  min-width: 600px; /* 必要なだけ確保 */
}

/* ===== グリッドレイアウト ===== */
.grid {
  display: grid;
  gap: var(--gap);
  grid-template-columns: 1fr;
}
@media (min-width: 600px) {
  .grid { grid-template-columns: repeat(2, 1fr); }
}
@media (min-width: 900px) {
  .grid { grid-template-columns: repeat(3, 1fr); }
}

.btn-space {
  margin-right: calc(100% / 30);
}

.article-space {
  margin-right: calc(100% / 60);
}

.flex {
  display: flex;
  justify-content: left;
}

.image {
  max-width: 100%;
  height: auto;
  border-radius: var(--radius);
}

.icon {
  /* 動的にfont-sizeを親要素の横幅めいいっぱいに調節 */
  font-size: large;
}